import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

var model3;

function uploadDataToServer(text,probabilities) {
    var d = new Date();
    var n = d.getTime();

    var apiURL = 'https://script.google.com/macros/s/AKfycbwK3EEp-8NU7wLEXLrZesp7XcjUYh7H6zdgQj7vBfDpDlwa13gl/exec?'
    apiURL += "timest="+n
    apiURL += "&sentence="+text
    apiURL += "&up="+probabilities[0]
    apiURL += "&cmd="+probabilities[1]
    apiURL += "&neutral="+probabilities[2]
    apiURL += "&lp="+probabilities[3]
    apiURL += "&qu="+probabilities[4]
    apiURL += "&bd="+probabilities[5]
    apiURL += "&rf="+probabilities[6]
    apiURL += "&nt="+probabilities[7]

    const formData = {};
    console.log("uploading data")
    fetch(apiURL).then((response) => response.text())
      .then((responseData) => { 
        console.log("response: ");
        console.log(responseData); })
      .catch((err) => { 
        console.log(err); 
      });
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      participantid: Date.now(),
      sentences: [{text:"What are you doing?",pred:[0,0,0,0,0,1,0,0]}]
    }
  }

  async componentDidMount() {

    const threshold = 0.7;
    const labelsToInclude = ["unlabelled_praise","command","neutral_talk","labelled_praise","question","behavior_description","reflection","negative_talk"];

    window.toxicity.load(threshold, labelsToInclude).then(model2 => {
      model3 = model2
    });
    
  }
  componentWillUnmount() {}

  classifyText = () => (event) => {
    var thiz = this;
    console.log(document.getElementById("classify-new-text-input").value)
    var sentence = document.getElementById("classify-new-text-input").value;
    model3.classify([sentence])
      .then(predictions => {
        console.log(predictions)
        var preds = []
        var probas = []
        var maxi = 0
        var maxv = 1

        for(var i = 0; i < 8; i++) {
          //predictions[i].results[0].probabilities[0]
          if (predictions[i].results[0].probabilities[0] < maxv){
            maxi = i
            maxv = predictions[i].results[0].probabilities[0]
          }
          console.log(predictions[i].results[0].probabilities[0])
          if (predictions[i].results[0].probabilities[0] > 0.9) {
            preds.push(1)
          } else {
            preds.push(0)
          }
          probas.push(predictions[i].results[0].probabilities[0])
        }
        preds = [0,0,0,0,0,0,0,0]
        preds[maxi] = 1
        uploadDataToServer(sentence,probas)
        console.log(preds)
        //var preds = [0,0,0,0,1,0,0,0]
        var cur_sentences = thiz.state.sentences
        cur_sentences.unshift({text:sentence,pred:preds})
        thiz.setState({sentence: cur_sentences})
        return true;
      });
  }

  render() {
    return (
      <div className="main">          
    <h1>DPICS classifier demo</h1>
    <div className="description">
      This is a demo of the text classification model that classifies parent-child interaction according to the DPICS. The samples below illustrate the classification output of the model.
    </div>
    <p>Enter your sentence below and click 'Classify' to classify it according to the DPICS.</p>
    <div>
      <input id="classify-new-text-input" placeholder="i.e. 'Come play with me'"/>
      <div id="classify-new-text" onClick={this.classifyText()}>Classify</div>
    </div>
    <div id="table-wrapper">
      <div className="row">
        <div className="text">Parent Sentence</div>

        <div className="label">Behavior Description</div>
        <div className="label">Command</div>
        <div className="label">Labelled Praise</div>
        <div className="label">Negative Talk</div>
        <div className="label">Neutral Talk</div>
        <div className="label">Question</div>
        <div className="label">Reflection</div>
        <div className="label">Unlabelled Praise</div>
      </div>
      {this.state.sentences.map((value, key) => (
        <div className="row">
          <div className="text">{value.text}</div>
          {value.pred[0] == 1 ? <div className="label positive">Likely</div> : <div className="label">Unlikely</div>}
          {value.pred[1] == 1 ? <div className="label positive">Likely</div> : <div className="label">Unlikely</div>}
          {value.pred[2] == 1 ? <div className="label positive">Likely</div> : <div className="label">Unlikely</div>}
          {value.pred[3] == 1 ? <div className="label positive">Likely</div> : <div className="label">Unlikely</div>}
          {value.pred[4] == 1 ? <div className="label positive">Likely</div> : <div className="label">Unlikely</div>}
          {value.pred[5] == 1 ? <div className="label positive">Likely</div> : <div className="label">Unlikely</div>}
          {value.pred[6] == 1 ? <div className="label positive">Likely</div> : <div className="label">Unlikely</div>}
          {value.pred[7] == 1 ? <div className="label positive">Likely</div> : <div className="label">Unlikely</div>}
        </div>
      ))}
    </div>
          <h2>The related paper is published at PervasiveHealth 2019:</h2>
          <p>We studied the classifier as a tool to help parents communicate. Make sure to check out our <a href="https://scholar.harvard.edu/files/bhb/files/specialtime_final_pervasivehealth19.pdf">paper</a> for more details.</p>
          <p>Created by: <a href="https://scholar.harvard.edu/bhb">Bernd Huber</a></p>
      </div>
    );
  }
}
